import React, { FC, useEffect, useRef, useState } from 'react';
import SearchIcon from '../Icons/SearchIcon/SearchIcon';
import { TextInput } from '../Form';
import { AnimatePresence, motion } from 'framer-motion';
import { useSearchBox } from 'react-instantsearch';
import { useDebounce } from '../../../hooks/useDebounce';

type TProps = {
  isSearchInputVisible: boolean;
  searchValue: string;
  setSearchValue: (value: string) => void;
  setIsSearchInputVisible: (value: boolean) => void;
};

export const SearchBox: FC<TProps> = ({
  isSearchInputVisible,
  searchValue,
  setSearchValue,
  setIsSearchInputVisible
}) => {
  const { query, refine } = useSearchBox();

  const debouncedSearchValue = useDebounce(searchValue, 500);

  useEffect(() => {
    refine(debouncedSearchValue);
  }, [debouncedSearchValue]);

  const handleInputClear = () => {
    if (!searchValue) setIsSearchInputVisible(false)
    setSearchValue('');
    refine('');
  };

  return (
    <AnimatePresence>
      {isSearchInputVisible && (
        <motion.div
          initial={{ width: 0, opacity: 0 }}
          animate={{ width: 250, opacity: 1 }}
        >
          <TextInput
            placeholder="Search"
            id="general-search-input"
            prefixContent={<SearchIcon />}
            onClear={handleInputClear}
            value={searchValue}
            onChange={(e: React.ChangeEvent<HTMLInputElement>) =>
              setSearchValue(e.currentTarget.value)
            }
          ></TextInput>
        </motion.div>
      )}
    </AnimatePresence>
  );
};
