import React, { FC } from 'react';
import { Link } from 'gatsby';
import {
  Highlight,
  Hits,
  Index,
  PoweredBy,
} from 'react-instantsearch';
import { ResultsByIndex, StyledPowerdBy, StyledSearchResults } from './styled';
import { Text } from '../Text';

enum AlgoliaIndexes {
  News = 'news',
  terms = 'terms',
  Policies = 'policies',
  Bios = 'bios',
}

type TCustomHit = {
  title: string;
  slug: string;
}

type PageHitProps = {
  hit: TCustomHit;
  indexName: string;
}

const getUrl = (slug: string, indexName: string) => {
  if (indexName === "terms") return `/glossary/${slug}`
  return `/${indexName}/${slug}`
}

const PageHit:FC<PageHitProps> = ({ hit, indexName}) => (
    <a href={getUrl(hit.slug, indexName)} target='_blank' key={hit.slug}>
      <Text.Regular.Normal.sm>
        <Highlight attribute="title" hit={hit as Record<string, any>} />
      </Text.Regular.Normal.sm>
    </a>
);

const HitsInIndex = ({ indexName }: { indexName: string }) => (
  <Index indexName={indexName}>
    <Hits className="Hits" hitComponent={(props: { hit: TCustomHit }) => <PageHit {...props} indexName={indexName}/>} />
  </Index>
);

const SearchResult = () => (
  <StyledSearchResults>
    <ResultsByIndex>
      {Object.values(AlgoliaIndexes).map((indexName) => (
        <HitsInIndex indexName={indexName} key={indexName} />
      ))}
    </ResultsByIndex>
    <StyledPowerdBy>
      <PoweredBy />
    </StyledPowerdBy>
  </StyledSearchResults>
);

export default SearchResult;
