import React, { InputHTMLAttributes, SelectHTMLAttributes } from 'react';
import { FC } from 'react';
import DropdownArrowIcon from '../Icons/DropdownArrowIcon/DropdownArrowIcon';
import {
    ClearIconContainer,
    InputPrefix,
  StyledDropdown,
  StyledDropdownInput,
  StyledTextInput,
  TextInputContainer,
} from './styled';
import CrossIcon from '../Icons/CrossIcon/CrossIcon';
import { colors } from '../../../utils/colors';

export const DropdownInput: FC<SelectHTMLAttributes<{}>> = ({ children, ...props }) => {
  return (
    <StyledDropdown>
      <StyledDropdownInput {...props}>{children}</StyledDropdownInput>
      <DropdownArrowIcon></DropdownArrowIcon>
    </StyledDropdown>
  );
};

export const TextInput: FC<InputHTMLAttributes<{}> & { prefixContent?: React.ReactElement, onClear?: () => void }> = ({
  prefixContent,
  onClear,
  ...props
}) => {
  return (
    <TextInputContainer>
      {prefixContent && <InputPrefix>{prefixContent}</InputPrefix>}
      <StyledTextInput {...props} withPrefix={!!prefixContent}></StyledTextInput>
      {onClear && <ClearIconContainer onClick={onClear}><CrossIcon color={colors.primary.gray500}/></ClearIconContainer>}
    </TextInputContainer>
  );
};
