import React, { FC, PropsWithChildren, useContext, useState } from 'react';
import { useCallback } from 'react';
import styled from 'styled-components';
import { ContentContainer } from '../Container/Container';
import ExternalScriptsLoader from '../ExternalScriptsLoader';
import Footer from '../Footer/Footer';
import Header from '../Header/Header';
import HeaderPromoBanner from '../HeaderPromoBanner';
import { device } from '../../../utils/layoutBreakpoints';
import { GlobalContext } from '../../../context/globalContext';

interface Props {
  disabledContainer?: boolean;
}

const StyledLayout = styled.div`
  height: 100%;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
`;

const Main = styled.main`
  height: 100%;
  flex: 1 0 auto;
  @media ${device.laptop} {
    margin-top: 0;
  }
`;

interface AppContextInterface {
  hubSpot: {
    isHubSpotFormsLoaded: boolean;
    setIsHubSpotFormsLoaded: (value: boolean) => void;
  };
}

export const AppContext = React.createContext<AppContextInterface>({
  hubSpot: {
    isHubSpotFormsLoaded: false,
    setIsHubSpotFormsLoaded: () => {},
  },
});

const Layout: FC<PropsWithChildren<Props>> = ({ children, disabledContainer }) => {
  const [isHubSpotFormsLoaded, setIsHubSpotFormsLoaded] = useState(false);

  const appContextValue = {
    hubSpot: {
      isHubSpotFormsLoaded,
      setIsHubSpotFormsLoaded,
    },
  };

  const globalContext = useContext(GlobalContext);

  const handleBannerHide = useCallback(() => {
    sessionStorage.setItem('hideBanner', 'true');
    globalContext.setHideHeaderBanner(true);
  }, []);

  return (
    <AppContext.Provider value={appContextValue}>
      <HeaderPromoBanner onClick={handleBannerHide} hideBanner={globalContext.hideHeaderBanner} />
      <StyledLayout>
        <Header />
        <Main>
          {disabledContainer ? children : <ContentContainer>{children}</ContentContainer>}
        </Main>
        <Footer />
      </StyledLayout>
      <ExternalScriptsLoader />
    </AppContext.Provider>
  );
};

export default Layout;
