import { GatsbySeo, JsonLd } from 'gatsby-plugin-next-seo';
import React, { FC } from 'react';
import { Helmet } from 'react-helmet';
import DesktopLogo from '../../../images/Logo/Telgorithm/DesktopLogo.svg';

type Props = {
  url: string;
};

export const TopLevelPageJSONLDSchemas: FC<Props> = ({ url }) => {
  return (
    <>
      <JsonLd
        json={{
          '@context': 'https://schema.org',
          '@type': 'WebSite',
          name: 'Telgorithm',
          url: url,
        }}
      />
      <JsonLd
        json={{
          '@context': 'https://schema.org',
          '@type': 'Corporation',
          name: 'Telgorithm',
          url: url,
          logo: DesktopLogo,
          sameAs: [
            'https://www.facebook.com/Telgorithm/',
            'https://twitter.com/telgorithm',
            'https://www.linkedin.com/company/telgorithm',
          ],
        }}
      />
    </>
  );
};

export const GatsbySeoComponent: FC<{ canonicalUrl: string }> = ({ canonicalUrl }) => {
  return <GatsbySeo canonical={canonicalUrl} />;
};

export const MetaData: FC<{
  pageTitle?: string;
  title: string;
  description: string;
  featureImage?: string;
}> = ({ pageTitle, title, description, featureImage }) => {
  return (
    <Helmet title={pageTitle ?? title}>
      <meta name='title' content={title} />
      <meta property="og:title" content={title} />
      <meta property="twitter:title" content={title} />
      <meta name="description" content={description} />
      <meta property="og:description" content={description} />
      <meta property="twitter:description" content={description} />
      {featureImage && <meta property="og:image" content={featureImage} />}
      {featureImage && <meta name="twitter:image" content={featureImage} />}
    </Helmet>
  );
};
