import React from 'react';
import { useInView } from 'react-cool-inview';
import FooterLogoImage from '../../../images/Logo/Telgorithm/FooterLogo.svg';

const FooterLogo = () => {
  const { observe, inView } = useInView({});
  return (
    <div ref={observe} style={{ width: 192, height: 32 }}>
      {inView && (
        <img src={FooterLogoImage} alt="Telgorithm" width={192} height={32}></img>
      )}
    </div>
  );
};

export default FooterLogo;
