import { Link } from 'gatsby';
import React, { FC, useState, useEffect } from 'react';
import styled from 'styled-components';
import { colors } from '../../../utils/colors';
import { device } from '../../../utils/layoutBreakpoints';
import BurgerMenuIcon from '../Icons/BurgerMenuIcon/BurgerMenuIcon';
import MainNavigation from './MainNavigation/MainNavigation';
import ServiceNavigation from './DesktopNavigation/ServiceNavigation/ServiceNavigation';
import MobileNavigation from './MobileNavigation/MobileNavigation';
import { InternalLinks } from '../../../utils/links';
import {
  headerNavigationItems,
  HeaderNavigationSections,
} from './HeaderNavigation/HeaderNavigationItem/headerNavigationItems';
import { AppContext } from '../Layout/Layout';
import HeaderLogo from '../Logo/HeaderLogo';
import MobileLogo from '../Logo/MobileLogo';
import SearchIcon from '../Icons/SearchIcon/SearchIcon';
import { GeneralSearch } from '../GeneralSearch';

const StyledHeader = styled.header`
  &.navOpen {
    .mobileCurtain {
      height: 100vh;
      opacity: 0.4;
      visibility: visible;
      transition: opacity 0.4s ease-in-out, visibility 0s linear 0s, height 0s linear 0s;
    }
    .menuContainer {
      height: 100vh;
      transition: height 0.4s cubic-bezier(.5,0,.5,1);
    }
  }

  width: 100%;
  height: auto;
  box-sizing: border-box;
  color: ${colors.primary.black};
  @media ${device.laptop} {
    height: auto;
    padding-top: 0;
  }
  position: sticky;
  z-index: 10;
  top: 0;
  left: 0;
  @media ${device.laptop} {
    transition: all 0s;
  }
`;

const MenuContainer = styled.div`
  height: 52px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  background-color: ${colors.primary.white};
  box-shadow: 0px 5px 10px rgba(40, 42, 59, 0.1), 0px 1px 0px rgba(40, 42, 59, 0.05);
  transition: height 0.4s cubic-bezier(.3,0,.5,1);
  position: relative;
  @media ${device.laptop} {
    height: 64px;
    padding: 0px 30px;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    box-shadow: 0px 5px 20px rgba(40, 42, 59, 0.1), 0px 1px 0px rgba(40, 42, 59, 0.05);
    transition-duration: 0s;
  }
`;

const DesktopHeaderWrapper = styled.div`
  justify-content: space-between;
  display: none;
  width: 100%;
  max-width: 1268px;
  height: 64px;
  @media ${device.laptop} {
    display: flex;
  }
`;

const MobileHeaderWrapper = styled.div`
  font-family: 'Rubik';
  font-size: 0.8rem;
  font-weight: 500;
  height: 52px;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  & > div:first-child {
    a {
      height: 100%;
      padding: 12px;
      display: block;
    }
    img {
      height: 28px;
      width: 28px;
      display: block;
    }
  }
  & > div:last-child {
    height: 100%;
    display: flex;
    align-items: center;
    .getDemoBtn {
      height: 100%;
      display: flex;
      align-items: center;
      padding: 0px 6px 0px 12px;
    }
  }
  @media ${device.laptop} {
    display: none;
  }
`;

const MobileBurgerBtn = styled.div`
  width: 52px;
  height: 52px;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0 !important;
`;

const Wrapper = styled.div`
  display: flex;
  gap: 24px;
  align-items: center;
`;

const MobileCurtain = styled.div`
  width: 100vw;
  height: 0;
  background: #000000;
  display: block;
  top: 0;
  left: 0;
  position: absolute;
  opacity: 0;
  z-index: -10;
  visibility: hidden;
  transition: opacity 0.4s cubic-bezier(.5,0,.1,1), visibility 0s linear 0.5s, height 0s linear 0.5s;
  @media ${device.laptop} {
    display: none;
  }
`;

const MobileNavButtons = styled.div`
  display: flex;
  gap: 10px;
`

const Header: FC = () => {
  const [isMobileNavigationOpened, setIsMobileNavigationOpened] =
    useState<boolean>(false);

  const loginLink = headerNavigationItems
    .find(
      (section) => section.sectionTitle === HeaderNavigationSections.ServiceNavigation,
    )
    ?.links.find((link) => link.linkId === 'header_navigation_link_login');

  useEffect(() => {
    const handleResize = () => {
      if (window.innerWidth > 1100) {
        setIsMobileNavigationOpened(false);
      }
    };
    window.addEventListener('resize', handleResize);
    return () => {
      window.removeEventListener('resize', handleResize);
    };
  }, []);

  const handleBurgerIconClick = () => {
    setIsMobileNavigationOpened(st => !st)
  }

return (
    <StyledHeader className={isMobileNavigationOpened ? 'navOpen' : ''}>
      <MobileCurtain className='mobileCurtain'></MobileCurtain>
      <MenuContainer className='menuContainer'>
        <DesktopHeaderWrapper>
          <Wrapper>
            <Link to={InternalLinks.HomePage}>
              <HeaderLogo />
            </Link>
            <MainNavigation />
          </Wrapper>
          <Wrapper>
            <GeneralSearch/>
            <ServiceNavigation />
          </Wrapper>
        </DesktopHeaderWrapper>
        <MobileHeaderWrapper>
          <div>
            <Link to={InternalLinks.HomePage}>
              <MobileLogo />
            </Link>
          </div>
          <MobileNavButtons>
            <GeneralSearch></GeneralSearch>
            <MobileBurgerBtn onClick={handleBurgerIconClick} id="nav_burger_button">
              <BurgerMenuIcon isOpen={isMobileNavigationOpened} />
            </MobileBurgerBtn>
          </MobileNavButtons>
        </MobileHeaderWrapper>
        <MobileNavigation
          isOpen={isMobileNavigationOpened}
          setIsMobileNavigationOpened={setIsMobileNavigationOpened}
        />
      </MenuContainer>
    </StyledHeader>
  );
};

export default Header;
