import React, { FC, useEffect, useState } from 'react';
import styled from 'styled-components';
import { colors, opacityToHex } from '../../../utils/colors';
import { device } from '../../../utils/layoutBreakpoints';
import { Container } from '../Container/Container';
import FooterLogo from '../Logo/FooterLogo';
import FooterNavigation from './FooterNavigation/FooterNavigation';
import SeparatorLine from './SeparatorLine/SeparatorLine';
import Socials from './Socials/Socials';
import Subscribe from './Subscribe/Subscribe';

const StyledFooter = styled.footer`
  font-family: Rubik;
  font-weight: 400;
  color: ${colors.primary.white + opacityToHex['0.5']};
  background-color: ${colors.primary.black};
  font-size: 0.7rem;
  padding: 25px 0;
  @media ${device.laptop} {
    height: auto;
    padding: 50px 0;
  }
`;

const Wrapper = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: start;
  gap: 30px;
  @media ${device.laptop} {
    flex-direction: row;
    justify-content: space-between;
    gap: 50px;
  }
`;

const VerticalWrapper = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: space-between;
`;

const DesktopCopyright = styled.div`
  font-size: 0.6rem;
  display: none;
  @media ${device.laptop} {
    display: block;
  }
`;

const MobileCopyright = styled.div`
  font-size: 0.6rem;
  display: block;
  margin-top: 20px;
  @media ${device.laptop} {
    display: none;
  }
`;

const FooterLogoWrapper = styled.div`
  display: flex;
  gap: 15px;
  align-items: center;
`

const Footer: FC = () => {
  const [isSourceForgeScriptLoaded, setIsSourceForgeScriptLoaded] = useState(false);
  useEffect(() => {
    const sourceForgeScript = document.createElement('script');
    sourceForgeScript.type = 'text/javascript';
    sourceForgeScript.async = true;
    sourceForgeScript.src = 'https://b.sf-syn.com/badge_js?sf_id=3610907&variant_id=sf';
    sourceForgeScript.onload = () => setIsSourceForgeScriptLoaded(true);
    document.body.appendChild(sourceForgeScript);
  }, []);

  const copyrightText = 'Copyright © 2024 Telgorithm. All rights reserved.';
  return (
    <StyledFooter>
      <Container>
        <Wrapper>
          <FooterLogoWrapper>
            <FooterLogo />
            <div
              className="sf-root"
              data-id="3610907"
              data-badge="customers-love-us-white"
              data-variant-id="sf"
              style={{ width: '50px', display: isSourceForgeScriptLoaded ? "block" : "none" }}
            >
              <a
                href="https://sourceforge.net/software/product/Telgorithm/"
                target="_blank"
              >
                Telgorithm Reviews
              </a>
            </div>
          </FooterLogoWrapper>
          <div>
            <Socials />
          </div>
        </Wrapper>
        <SeparatorLine margin="30px 0" />
        <Wrapper>
          <VerticalWrapper>
            <Subscribe />
            <DesktopCopyright>{copyrightText}</DesktopCopyright>
          </VerticalWrapper>
          <FooterNavigation></FooterNavigation>
        </Wrapper>
        <MobileCopyright>{copyrightText}</MobileCopyright>
      </Container>
    </StyledFooter>
  );
};

export default Footer;
